
@import url('https://fonts.googleapis.com/css?family=Oswald');
* {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}
body {
  font-family: 'Oswald', sans-serif;
}
a {
  text-decoration: none;
}
i {
  cursor: pointer;
}
header {
  padding: 1em;
  position: relative;
}
section {
  padding: 1.5em 1em;
}
.container {
  max-width: 500px;
  width: 100%;
  margin: 40px auto;
}
.container h1 {
  font-size: 2em;
  text-align: center;
  text-transform: uppercase;
  padding: 1em;
}
.container h1 span {
  color: #0CC92B;
}
header input {
  -webkit-box-flex: 1;
      -ms-flex: 1;
          flex: 1;
  padding: 0 10px;
  width: 95%;
  height: 30px;
  outline: none;
  border-radius: 5px;
  border: 1px solid #C6C6C6;
}
header .addBtn {
  width: 40px;
  height: 40px;
  border-radius: 50%;
  overflow: hidden;
  position: absolute;
  top: 50%;
  right: 15px;
  z-index: 10;
  -webkit-transform: translateY(-50%);
          transform: translateY(-50%);
}
header .addBtn i {
  display: block;
  -webkit-box-flex: 40px;
      -ms-flex: 40px;
          flex: 40px;
  height: 40px;
  text-align: center;
  line-height: 40px;
  color: #FFF;
  font-size: 1.5em;
  background-color: #0CC92B;
}
.inputbox {
  -webkit-transform: scaleX(0);
          transform: scaleX(0);
  -webkit-transform-origin: right center;
          transform-origin: right center;
  -webkit-transition: all .4s ease-out;
  transition: all .4s ease-out;
  opacity: 0;
}
.extend {
  -webkit-transform: scaleX(1);
          transform: scaleX(1);
  opacity: 1;
}
.todos li {
  margin: 0 0 10px 0;
  padding: 10px;
  border-bottom: 1px dotted #C6C6C6;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
}
.todos .fa-undo {
  color: #0CC92B;
}
.todos .fa-check {
  color: #0CC92B;
}
.todos .fa-close {
  color: #E20A16;
}
.complete {
  text-decoration: line-through;
  color: #E20A16;
}
.list-ul {
  -webkit-transition: all .7s;
  transition: all .7s;
}

/* Add task transition */
.list-enter-active,
.list-leave-active {
  -webkit-transition: opacity .7s, -webkit-transform .7s;
  transition: opacity .7s, -webkit-transform .7s;
  transition: opacity .7s, transform .7s;
  transition: opacity .7s, transform .7s, -webkit-transform .7s;
}
.list-enter,
.list-leave-to
/* .list-leave-active below version 2.1.8 */

{
  opacity: 0;
  -webkit-transform: translateY(-10px);
          transform: translateY(-10px);
}



